.container {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, .8) 50%,
    rgba(0, 0, 0, 1) 100%,
    rgba(0, 0, 0, .8) 15%
  );
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
}
.contant {
  width: 995px;
  height: 600px;
  /* border: 1px solid #ccc; */
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 30px 10px #ccc;
  background-color: #ccc;
}
.poster_modal {
  width: 482px;
  height: 580px;
  border-radius: 10px;
}
.info_modal {
  width: 482px;
  height: 580px;
  /* border: 1px solid #ccc; */
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #000;
}
.img_allInfo {
  width: 482px;
  height: 580px;
  border-radius: 10px;
}
.title,
.year,
.released, .plot {
  color: #ccc;
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  border-bottom: .5px dashed rgb(44, 43, 43);

}
.plot{
    color:khaki;
    border-bottom:none;
}